<template>
  <dl class="dl-horizontal">
    <dt></dt>
    <dd>{{user.username}}</dd>
    <dt></dt>
    <dd>{{user.gender}}</dd>
    <dt></dt>
    <dd>{{user.role}}</dd>
    <dt></dt>
    <dd>{{user.mobile}}</dd>
    <dt></dt>
    <dd>{{user.email}}</dd>
    <dt></dt>
    <dd>{{user.create_time | time}}</dd>
    <dt></dt>
    <dd>{{user.update_time | time}}</dd>
  </dl>
</template>

<script>
  import {
    site
  } from '../../libs/ajax'
  export default {
    data() {
      return {
        user: {}
      }
    },
    mounted() {
      let userId = this.$route.params.userId
      site.get('users/' + userId).then((res) => {
        let _user = res.data
        _user.gender = _user.gender === 1 ? '' : ''
        _user.role = _user.role === 'user' ? '' : ''
        this.user = _user
      })
    }
  }
</script>




